import LinkingHelper from "common/LinkingHelper";
import {
  StackActions,
  useFocusEffect,
  useIsFocused,
  useNavigation
} from "@react-navigation/native";
import { useCallback, useRef, useState } from "react";
import { checkEmailRegister } from "store/urStaff/services";
import { validateEmailString } from "utils";
import { getStaff } from "store/urStaff/services";

export const useUrstaffRegistration = () => {
  const navigation = useNavigation();
  // const dispatch = useDispatch();
  const [email, setEmail] = useState("");
  const [message, setMessage] = useState("");
  const [loading, setLoading] = useState(false);
  const [initLoading, setInitLoading] = useState(true);
  const [staffDetail, setStaffDetail] = useState();
  const inputRef = useRef();

  const isFocused = useIsFocused();

  useFocusEffect(
    useCallback(() => {
      if (isFocused) {
        getStaff().then((res) => {
          setInitLoading(false);
          if (res.OK) {
            setStaffDetail(res);
            return;
          }
        });
      }
    }, [isFocused])
  );

  const onPressBanner = () => {
    try {
      LinkingHelper.openURL("https://forms.urbox.vn/urstaff");
    } catch (error) {
      console.log(error);
    }
  };
  const onSubmit = () => {
    if (email && validateEmailString(email)) {
      setLoading(true);
      checkEmailRegister(email).then((res) => {
        setLoading(false);
        if (res.OK) {
          navigation.navigate("UrStaffAdvanceConfirmOTPScreen", {
            confirmRegister: true,
            email,
            userInfo: {
              companyEmail: email
            }
          });
          return;
        }
        setMessage(res.message);
        inputRef.current.doValidate();
      });
    }
  };

  const onChangeText = (text) => {
    setEmail(text);
  };

  return {
    inputRef,
    email,
    loading,
    message,
    initLoading,
    staffDetail,
    onChangeText,
    onSubmit,
    onPressBanner
  };
};
