import React from "react";
import { View, Text, ScrollView, ActivityIndicator } from "react-native";
import FeatherIcon from "react-native-vector-icons/Feather";
import ImageComponent from "components/ImageComponent";
import Gap from "components/Gap";
import CommonInput from "components/CommonInput";
import { validateEmailString } from "utils";
import { TouchableOpacity } from "react-native-gesture-handler";
import I18n from "translations";
import { useSafeAreaInsets } from "react-native-safe-area-context";
import { useUrstaffRegistration } from "../hooks/useUrstaffRegistration";
import { StyleSheet } from "react-native";
import { Colors, Metrics } from "themes";
import { deviceWidth, WIDTH_RATIO } from "themes/Dimens";
import { useNavigation } from "@react-navigation/native";
import ProgramBlock from "./ProgramBlock";
import { get } from "lodash";
import BlockSkeleton from "./BlockSkeleton";
import images from "themes/Images";

const UrStaffEmailRegistrationScreen = () => {
  const insets = useSafeAreaInsets();
  const {
    inputRef,
    loading,
    message,
    email,
    initLoading,
    staffDetail,
    onChangeText,
    onSubmit,
    onPressBanner
  } = useUrstaffRegistration();

  const navigation = useNavigation();

  if (initLoading) {
    return (
      <View style={styles.pad16}>
        <BlockSkeleton />
      </View>
    );
  }

  if (staffDetail) {
    const logo = get(staffDetail, "publisher.logo");
    return (
      <View style={styles.pad16}>
        <ProgramBlock
          program={{
            title: true ? "Chương trình UrStaff." : "Chương trình UrStaff.",
            logo: logo ? { uri: logo } : images.logoUrBox,
            banner: {
              uri:
                get(staffDetail, "card.cardTemplateImage") ||
                "https://urbox-hcm-prod.sgp1.cdn.digitaloceanspaces.com/collection-banners/urstaff_banner-1656573933492.png"
            },
            onPress: () => navigation.navigate("UrStaffOverviewScreen"),
            featureEnable: true,
            brandName: get(staffDetail, "campaign.title")
          }}
        />
      </View>
    );
  }

  return (
    <View style={styles.container}>
      {/* <UrboxHeader
        containerStyles={styles.header}
        title={I18n.t("Login")}
        backOnPress={navigation.goBack}
      /> */}
      <ScrollView style={styles.body}>
        <Gap.L />
        <TouchableOpacity onPress={onPressBanner}>
          <ImageComponent
            source={{
              uri: "https://urbox-hcm-prod.sgp1.cdn.digitaloceanspaces.com/collection-banners/urstaff_banner-1656573933492.png"
            }}
            style={styles.banner}
          />
        </TouchableOpacity>
        <Gap.L />

        <View style={styles.row}>
          <FeatherIcon name="alert-circle" color="#1074FF" size={22} />
          <Text style={styles.description}>{I18n.t("UrStaffEmailNote")}</Text>
        </View>
        <Gap.L />
        <CommonInput
          ref={inputRef}
          label="Email"
          autoCapitalize={"none"}
          rules={[
            {
              validator: validateEmailString,
              message: I18n.t("EcommerceAddressScreen.ErrorPhoneEmail"),
              // validateOnChange: true,
              validateOnBlur: true
            },
            {
              validator: (text) => text.length > 0,
              message: I18n.t("RequireEmail"),
              // validateOnChange: true,
              validateOnBlur: true
            },
            {
              validator: () => !loading && !message,
              message
            }
          ]}
          placeholder={I18n.t("CompanyEmail")}
          value={email}
          onChangeText={onChangeText}
        />
      </ScrollView>
      <TouchableOpacity
        onPress={onSubmit}
        disabled={loading}
        style={[
          styles.footer,
          { marginBottom: insets?.bottom + 6 },
          !loading && { backgroundColor: Colors.primary }
        ]}>
        <Text style={styles.btnText}>
          {loading && (
            <ActivityIndicator
              style={styles.marginRight}
              color={Colors.white}
            />
          )}
          {I18n.t("Confirm")}
        </Text>
      </TouchableOpacity>
    </View>
  );
};

const BANNER_WIDTH = deviceWidth - Metrics.baseListMarginHorizontal * 2;

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "white",
    paddingTop: 24
  },
  header: {
    height: 100,
    alignItems: "flex-start"
  },
  body: {
    backgroundColor: "white",
    marginTop: -20,
    borderTopLeftRadius: Metrics.baseBorderRadius * 2,
    borderTopRightRadius: Metrics.baseBorderRadius * 2,
    paddingHorizontal: Metrics.baseListMarginHorizontal
  },
  banner: {
    width: BANNER_WIDTH,
    height: BANNER_WIDTH / 2.22,
    borderRadius: Metrics.baseBorderRadius
  },
  row: {
    flexDirection: "row"
  },
  description: {
    color: Colors.mediumGrey,
    flex: 1,
    fontSize: WIDTH_RATIO * 15,
    marginLeft: 10,
    fontWeight: "400"
  },
  footer: {
    height: 46,
    backgroundColor: Colors.gray,
    marginHorizontal: Metrics.baseListMarginHorizontal,
    borderRadius: Metrics.baseBorderRadius,
    justifyContent: "center",
    alignItems: "center"
  },
  btnText: {
    color: Colors.white,
    fontWeight: "bold",
    fontSize: 16
  },
  marginRight: {
    marginRight: 16
  },
  pad16: { paddingHorizontal: 15 * WIDTH_RATIO, paddingVertical: 24 }
});

export default UrStaffEmailRegistrationScreen;
